<template>
    <div class="characters fullscreen bg-dark-2" v-if="$store.state.account">
        <div class="main row inline font-primary font-normal q-mt-xl">
            <div class="col-12">
                <div class="text-h6 text-center">{{pages[selection]}}</div>
            </div>
            <div class="col-3">
                <div class="menu q-pl-md row column q-px-xl h-52">
                    <q-btn label="Ana Sayfa" color="secondary" text-color="primary"  class="col-3" flat @click="selection = 0"/>
                    <q-btn label="Güncelleme Notları" color="secondary" text-color="primary" class="col-3" flat @click="selection = 1"/>
                    <q-btn label="Karakterlerim" color="secondary" text-color="primary" class="col-3" flat @click="selection = 2"/>
                </div>
            </div>

            <div class="col-8 q-pt-xl">
                <div class="page_main" v-if="selection == 0">
                    <div class="text-h6">Hoşgeldiniz, {{$store.state.account.username}}.</div>
                    <div class="text-h4 q-pt-xl" >Doldurulacak.</div>
                </div>

                <div class="page_updates q-px-lg q-pb-md" v-if="selection == 1">
                    <q-timeline dark color="secondary" v-if="updates.length > 0">
                        <q-timeline-entry heading>
                            Sunucu Güncellemeleri
                        </q-timeline-entry>                       

                        <q-timeline-entry v-for="update in updates" :key="update.Id"
                            :title="update.title"
                            :subtitle="update.date"
                            :color="(update.color) ? update.color : 'green'"
                            :icon="(update.icon) ? update.icon : 'home'"
                        >
                            <div>
                            {{ update.body }}
                            </div>
                        </q-timeline-entry>                        
                    </q-timeline>
                </div>

                <div class="pages_characters row inline full-width" v-if="selection == 2">
                    <div class="col-3 bg-dark-1 q-ma-xs row inline" v-for="character in characters" :key="character.Id">
                        <div class="img">
                            <q-img
                            src="https://placeimg.com/500/300/nature"
                            :ratio="1"
                            width="100px"
                            class="q-ma-md"
                            />
                        </div>
                        <div class="characterInfo q-mt-md col-7 text-secondary q-pr-sm">
                            <div class="text-center">{{JSON.parse(character.identify).name.replace('_', ' ') + ' (ID: ' + character.Id + ')'}}</div>
                            <hr>
                            <div class="font-light q-mt-sm"><span class="text-green">$</span> 200</div>
                            <div><q-icon name="location_on" color="green" /> {{JSON.parse(character.identify).birthLocation}}</div>
                            
                            <div class="playBtn q-my-md text-right">
                                <q-btn label="Oyna" color="accent" glossy @click="selectCharacter(character.Id)"/>
                            </div>
                        </div>
                    </div>

                    
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data: () => ({
        pages: ['Ana Sayfa', 'Güncelleme Notları', 'Karakterlerim'],
        selection: 0,
        characters: [],
        updates: []
    }),

    mounted() {
        if('alt' in window) {            
            // alt.on('S:Login:Complete', this.loginComplete)
            alt.emit('Plugin:Login:GetCharacterList');
            alt.emit('Plugin:Login:GetUpdates');
            alt.on('Plugin:Login:SendCharacterList', this.setCharacterList);
            alt.on('Plugin:Login:SetUpdates', this.setUpdates);
        }
    },

    methods: {
        setCharacterList(data) {
            if(data){
                this.characters = data;
            }
        },

        selectCharacter(id) {
            if('alt' in window)
                alt.emit('Plugin:Login:SelectCharacter', id); this.loginComplete();
        },

        loginComplete() {
            this.selection = 0;
            this.$router.push('/gui')
        },

        setUpdates(data) {
            this.updates = data;
        }
    }
}
</script>

<style lang="scss" scoped>
.main {
    width: 100vw;
}

.page_updates {
    overflow: auto;
    height: 80vh;
}::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #988C97;
}::-webkit-scrollbar
{
	width: 10px;
	background-color: #988C97;
}::-webkit-scrollbar-thumb
{
	background-color: #4B4E67;
	border: 2px solid #4B4E67;
}
</style>